* {
  padding: 0;
  margin: 0;
  box-sizing: inherit;
  //general reset
}

html {
  //This re-defines 1rem
  font-size: 62.5%; //1rem = 10px
  // font-size: 100%; //1rem = 10px
}

body {
  box-sizing: border-box;
  //changes calculation behavior

  font-family: sans-serif;
}

:root {
  --primary-light: #8abdff;
  --primary: #6d5dfc;
  --primary-dark: #5b0eeb;

  --white: #ffffff;
  --greyLight-1: #e4ebf5;
  --greyLight-2: #c8d0e7;
  --greyLight-3: #bec8e4;
  --greyDark: #9baacf;
}

$shadow: 0.3rem 0.3rem 0.6rem var(--greyLight-2), -0.2rem -0.2rem 0.5rem var(--white);
$inner-shadow: inset 0.2rem 0.2rem 0.5rem var(--greyLight-2),
  inset -0.2rem -0.2rem 0.5rem var(--white);

.App {
  display: flex;
  align-items: center;
  height: 100vh;
  margin: 0 auto;

  flex-direction: column;
  justify-content: center;
  padding: 2rem 4rem;
  border-radius: 2rem;
  background: var(--greyLight-1);
}
